header {
   .container {
      max-width: 1448px;
   }
}

.navbar {
   display: flex;
   justify-content: space-between;
   align-items: center;

   .header-logo {
      max-height: 117px;
   }
}

.burger-menu {
   cursor: pointer;
   padding: 15px;

   .burger-icon {
      width: 13px;
      height: 3px;
      background-color: #fff;
      margin: 6px 0;
      transition: 0.4s;
      border-radius: 20px;
      position: relative;

      span {
         display: inline-block;
         width: 20px;
         height: 3px;
         background-color: #fff;
         margin: 6px 0;
         border-radius: 20px;
         position: absolute;
         left: 0;

         &:first-of-type {
            width: 17px;
            top: -15px;
         }

         &:last-of-type {
            top: 3px;
         }
      }
   }
}

.navbar-collapse {
   display: flex;
   justify-content: center;
   flex-grow: 1;
   align-items: center;

   .navbar-nav {
      display: flex;
      align-items: center;
      justify-content: center;
      flex-grow: 1;

      a {
         color: $text-color;
         padding: 12px;
         margin: 0 30px;
         text-align: center;

         &:hover{
            color: #89c249;
         }
      }
       
      .nav-home {
         font-weight: 600;
         position: relative;

         &::after {
            content: "";
            display: inline-block;
            width: 15px;
            height: 2px;
            border-radius: 60px;
            background: #FFF;
            position: absolute;
            bottom: -0px;
            left: 50%;
            transform: translate(-50%, 0);
         }

         &:hover::after{
            background-color: #89c249;
         }
      }
   }

   .nav-button {
      color: $text-secondary;
      font-size: 14px;
      font-weight: 600;
      padding: 17px 35px;
      border-radius: 27px;
      border: 1px solid #FCB357;
      text-align: center;

      &:hover{
         border-color: #89C249;
         color: #89C249;
      }
   }
}